import React, { Component } from 'react';
import Nav from './components/NavBar';
import Main from './components/Main';
import Footer from './components/Footer';
import './styles.css';

class App extends Component {
  render() {
    return (
      <div className="App">
        <Nav />
        <br/>
        <Main />
        <br/>
        <Footer />
      </div>
    );
  }
}

export default App;


